/* Global Styles */
:root {
  --background-dark: #0a0a0a;
  --text-light: #f0f0f0;
  --accent-color: #61dafb; /* A subtle blue for highlights */
  --border-color: rgba(255, 255, 255, 0.1);
}

body {
  margin: 0;
  font-family: 'Inter', sans-serif; /* Modern sans-serif font, fallback to system */
  -webkit-font-smoothing: antialiased;
  -moz-osx-osx-font-smoothing: grayscale;
  overflow-x: hidden; /* Prevent horizontal scroll */
  background-color: var(--background-dark);
  color: var(--text-light);
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden; /* Ensure orb doesn't overflow */
}

/* Orb Container */
.orb-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1; /* Increased z-index */
  opacity: 0.5; /* Increased opacity for testing */ /* Reduced opacity */
}

.orb-fallback {
  background: linear-gradient(135deg, #3a1c71, #d76d77, #ffaf7b); /* Cool-toned gradient fallback */
  opacity: 0.5; /* Match orb opacity */
}

/* Content Wrapper */
.content {
  position: relative;
  z-index: 1;
  max-width: 1000px;
  padding: 60px 20px; /* Increased vertical padding */
  text-align: center;
  width: 100%;
  box-sizing: border-box; /* Include padding in width */
}

/* Hero Section */
.hero-section {
  min-height: 80vh; /* Make hero section take up most of the viewport */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.figment-logo {
  width: 200px; /* Increased size */
  height: auto;
  margin-bottom: 30px; /* Increased margin below logo */
}

.tagline {
  font-size: 1.8em; /* Slightly larger tagline */
  margin-top: 15px; /* Adjusted margin */
  margin-bottom: 50px; /* Increased margin below tagline */
  color: var(--text-light); /* Ensure tagline is white */
  max-width: 700px;
  line-height: 1.4;
}

/* GitHub Button */
.github-button {
  display: inline-block;
  padding: 18px 40px;
  background-color: white; /* Changed to white */
  color: black; /* Changed to black */
  text-decoration: none;
  border-radius: 50px;
  font-size: 1.3em;
  font-weight: bold;
  transition: background-color 0.3s ease, transform 0.3s ease, color 0.3s ease;
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.2);
}

.github-button:hover {
  background-color: #e0e0e0; /* Subtle gray on hover */
  color: black; /* Keep text black on hover */
  transform: translateY(-3px);
}

/* General Section Styling */
section {
  padding: 100px 0; /* Increased vertical padding for sections */
  margin-top: 0; /* Removed margin-top as padding handles spacing */
}

section:first-of-type {
  margin-top: 0;
}

section h2 {
  font-size: 3.5em; /* Slightly larger h2 */
  margin-bottom: 50px; /* Increased margin below h2 */
  font-weight: 700;
  color: var(--text-light); /* Ensure h2 is white */
}

section p {
  font-size: 1.3em; /* Slightly larger p */
  line-height: 1.8;
  color: var(--text-light); /* Ensure p is white */
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

/* Solution Details */
.solution-details {
  display: flex;
  justify-content: center;
  gap: 50px; /* Increased gap between cards */
  margin-top: 60px; /* Increased margin above cards */
  flex-wrap: wrap;
}

.cli-version, .web-version {
  flex: 1;
  min-width: 300px;
  max-width: 450px;
  padding: 40px; /* Increased padding inside cards */
  border-radius: 12px;
  background-color: rgba(255, 255, 255, 0.05); /* Subtle transparent white background */
  border: 1px solid rgba(255, 255, 255, 0.1); /* Subtle white border */
  backdrop-filter: blur(10px); /* Glassmorphism blur effect */
  -webkit-backdrop-filter: blur(10px); /* For Safari support */
  text-align: left;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2); /* More prominent shadow for cards */
}

.cli-version:hover, .web-version:hover {
  transform: translateY(-8px); /* More pronounced lift on hover */
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4); /* More pronounced shadow on hover */
}

.cli-version h3, .web-version h3 {
  font-size: 2em; /* Slightly larger h3 in cards */
  margin-top: 0;
  margin-bottom: 20px; /* Increased margin below h3 in cards */
  font-weight: 600;
  color: var(--text-light); /* Changed to white */
}

.cli-version p, .web-version p {
  font-size: 1.2em; /* Slightly larger p in cards */
  line-height: 1.7;
  color: var(--text-light); /* Ensure p inside cards is white */
  margin-left: 0;
  margin-right: 0;
}

.key-benefit {
  font-size: 1.6em; /* Slightly larger key benefit text */
  font-weight: bold;
  margin-top: 80px; /* Increased margin above key benefit */
  color: var(--text-light); /* Changed to white */
  max-width: 900px;
}

/* Footer */
.main-footer {
  padding: 40px 20px; /* Padding for the footer */
  text-align: center;
  margin-top: 60px; /* Space above the footer */
}

.linkedin-icon img {
  width: 32px; /* Icon size */
  height: 32px;
  filter: grayscale(100%) brightness(200%); /* Make it black and white */
  transition: filter 0.3s ease;
}

.linkedin-icon:hover img {
  filter: none; /* Revert to original colors if any */
}

/* How it Works Section */
.how-it-works-section {
  padding: 100px 0;
}

.workflow-steps {
  display: flex;
  justify-content: center;
  gap: 40px;
  margin-top: 60px;
  flex-wrap: wrap;
}

.step {
  flex: 1;
  min-width: 280px;
  max-width: 350px;
  padding: 30px;
  border-radius: 12px;
  background-color: rgba(255, 255, 255, 0.03); /* Slightly lighter background than cards */
  border: 1px solid rgba(255, 255, 255, 0.08); /* Subtle border */
  backdrop-filter: blur(8px); /* Glassmorphism blur */
  -webkit-backdrop-filter: blur(8px);
  text-align: center;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.step h3 {
  font-size: 1.8em;
  margin-top: 0;
  margin-bottom: 15px;
  font-weight: 600;
  color: var(--text-light); /* Changed to white */
}

.step p {
  font-size: 1.1em;
  line-height: 1.6;
  color: var(--text-light);
}

/* Responsive Adjustments */
@media (max-width: 1024px) {
  .tagline {
    font-size: 1.6em;
  }
  section h2 {
    font-size: 3em;
  }
  section p {
    font-size: 1.2em;
  }
  .cli-version, .web-version {
    max-width: none;
    width: 90%;
  }
  .step {
    max-width: none;
    width: 90%;
  }
}

@media (max-width: 768px) {
  .tagline {
    font-size: 1.4em;
  }
  .github-button {
    padding: 15px 30px;
    font-size: 1.1em;
  }
  section {
    padding: 80px 0;
  }
  section h2 {
    font-size: 2.5em;
  }
  section p {
    font-size: 1.1em;
  }
  .solution-details {
    flex-direction: column;
    gap: 40px;
    align-items: center; /* Center items horizontally in column layout */
  }
  .cli-version, .web-version {
    text-align: center;
  }
  .cli-version h3, .web-version h3 {
    text-align: center;
  }
  .key-benefit {
    font-size: 1.4em;
  }
}

@media (max-width: 480px) {
  .tagline {
    font-size: 0.9em; /* Further reduced for very small screens */
    margin-bottom: 30px; /* Adjusted margin */
  }
  .figment-logo {
    width: 120px; /* Further reduced logo size */
    margin-bottom: 20px; /* Adjusted margin */
  }
  .github-button {
    padding: 10px 20px; /* Smaller padding */
    font-size: 0.9em; /* Smaller font size */
  }
  section h2 {
    font-size: 1.8em; /* Further reduced h2 size */
    margin-bottom: 30px; /* Adjusted margin */
  }
  section p {
    font-size: 0.9em; /* Further reduced p size */
  }
  .solution-details, .workflow-steps {
    gap: 20px; /* Reduced gap for very small screens */
  }
  .cli-version, .web-version, .step {
    padding: 25px; /* Reduced padding */
  }
  .cli-version h3, .web-version h3, .step h3 {
    font-size: 1.5em; /* Reduced heading size in cards */
  }
  .cli-version p, .web-version p, .step p {
    font-size: 1em; /* Reduced paragraph size in cards */
  }
}
